import React, { useState, useEffect, useRef } from 'react';
import {
  View,
  Text,
  StyleSheet,
  Image,
  TextInput,
  TouchableOpacity,
  Dimensions,
} from 'react-native';

const VerificationScreen = ({navigation}) => {
  const [timer, setTimer] = useState(60);
  const [otp, setOtp] = useState(['', '', '', '']);
  const inputRefs = useRef([]);

  useEffect(() => {
    const interval = setInterval(() => {
      setTimer((prevTimer) => (prevTimer > 0 ? prevTimer - 1 : 0));
    }, 1000);
    return () => clearInterval(interval);
  }, []);

  const handleInputChange = (text, index) => {
    const newOtp = [...otp];
    newOtp[index] = text;
    setOtp(newOtp);

    if (text.length === 1 && index < inputRefs.current.length - 1) {
      inputRefs.current[index + 1]?.focus();
    }
  };

  const handleKeyPress = (e, index) => {
    if (e.nativeEvent.key === 'Backspace' && otp[index] === '' && index > 0) {
      inputRefs.current[index - 1]?.focus();
    }
  };

  return (
    <View style={styles.container}>
      <View style={styles.header}>
      <Image source={require('../assets/Registration.png')} style={styles.image} />
      </View>
      <View style={styles.formBackground}>
        <View style={styles.pageIndicator}>
          <View style={[styles.dot, styles.dot]} />
          <View style={[styles.dot, styles.dotActive]} />
          <View style={[styles.dot, styles.dot]} />
        </View>
        <View style={styles.otpContainer}>
          {otp.map((value, index) => (
            <TextInput
              key={index.toString()} // Ensure key is a string
              style={styles.otpInput}
              maxLength={1}
              keyboardType="numeric"
              value={value}
              onChangeText={(text) => handleInputChange(text, index)}
              onKeyPress={(e) => handleKeyPress(e, index)}
              ref={(ref) => (inputRefs.current[index] = ref)}
            />
          ))}
        </View>
        <Text style={styles.instructionText}>
          Enter Verification Code we have sent onto <Text style={styles.emailText}>abc@gmail.com</Text>
        </Text>
        <TouchableOpacity>
          <Text style={styles.resendText}>
            Resend code - {timer > 0 ? `00:${timer < 10 ? `0${timer}` : timer}` : '00:00'}
          </Text>
        </TouchableOpacity>
        <View style={styles.buttonContainer}>
          <TouchableOpacity style={styles.backButton} onPress={() => navigation.navigate('RegistrationScreen')}>
            <Text style={styles.backButtonText}></Text>
          </TouchableOpacity>
          <TouchableOpacity style={styles.nextButton} onPress={() => navigation.navigate('PasswordScreen')}>
            <Text style={styles.buttonText}>Next</Text>
          </TouchableOpacity>
        </View>
      </View>
    </View>
  );
};

const { width, height } = Dimensions.get('window');

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: '#1a1a2e',  // Set dark blue background color
  },
  header: {
    flex: 1,
    justifyContent: 'center',
    alignItems: 'center',
  },
  formBackground: {
    flex: 1,
    width: '100%',
    backgroundColor: '#FFFFFF',
    borderTopLeftRadius: 20,
    borderTopRightRadius: 20,
    paddingHorizontal: width * 0.05,
    paddingVertical: height * 0.03,
    alignItems: 'center',
    marginTop: height * 0.03,
  },
  image: {
    width: width * 0.8,
    height: height * 0.3,
    resizeMode: 'contain',
    marginTop: height * 0.05,
  },
  pageIndicator: {
    flexDirection: 'row',
    marginBottom: height * 0.02,
  },
  dot: {
    width: width * 0.020,
    height: width * 0.020,
    borderRadius: width * 0.0125,
    backgroundColor: '#888',
    marginHorizontal: width * 0.0100,
  },
  dotActive: {
    backgroundColor: '#000',
  },
  otpContainer: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    width: '80%',
    marginBottom: height * 0.03,
  },
  otpInput: {
    width: width * 0.15,
    height: width * 0.15,
    borderColor: '#ccc',
    borderWidth: 1,
    borderRadius: width * 0.03,
    textAlign: 'center',
    fontSize: width * 0.06,
    backgroundColor: '#fff',
  },
  instructionText: {
    fontSize: width * 0.04,
    color: '#666',
    textAlign: 'center',
    marginBottom: height * 0.015,
  },
  emailText: {
    color: '#000A23',
    fontWeight: 'bold',
  },
  resendText: {
    fontSize: width * 0.04,
    color: '#1a1a2e',
    textAlign: 'center',
    marginBottom: height * 0.04,
  },
  buttonContainer: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    width: '80%',
  },
  backButton: {
    backgroundColor: '#000A23',
    borderRadius: width * 0.03,
    padding: width * 0.02,
    alignItems: 'center',
    justifyContent: 'center',
    width: width * 0.15,
  },
  backButtonText: {
    color: '#FFFFFF',
    fontWeight: 'bold',
    fontSize: width * 0.05,
  },
  nextButton: {
    backgroundColor: '#000A23',
    borderRadius: width * 0.03,
    padding: width * 0.04,
    alignItems: 'center',
    justifyContent: 'center',
    width: width * 0.5,
  },
  buttonText: {
    color: '#FFFFFF',
    fontWeight: 'bold',
    fontSize: width * 0.05,
  },
});

export default VerificationScreen;
